@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap');

html {
    box-sizing: border-box;
}

*,
*::after,
*::before {
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}
*{
    box-sizing: border-box;
}
a {
    text-decoration: none;
    color: inherit;
}
ul {
    list-style-type: none;
}
body {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #FFFFFF;
}

html,
body {
    height: 100%;
    margin: 0;
    padding: 0;
}

input {outline:none;}

.container {
    max-width: 1130px;
    margin: 0 auto;
    padding: 0 10px;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.main {
    flex: 1;
    background-color: #1A1A1A;
}

.footer {
    flex-shrink: 0;
}

/*font-family: 'Playfair Display', serif;*/
/*font-family: 'Poppins', sans-serif;*/

/*========================HEADER===============*/
.navbar{
    font-size: 17px;
    line-height: 26px;
    background-color: #252525;
    border-bottom: 2px solid #2f2e2e;
}

.navbar-logo{
    color: #34C759;
    font-size: 20px;
    gap: 100px;
}

.navbar-logo__item{
    display: flex;
    align-items: center;
    gap: 10px;
}

.navbar-nav{
    align-items: center;
    display: flex;
    margin: 0 auto;
    padding: 35px 0;
    justify-content: space-between;
}

.nav-item {
    position: relative;
    display: inline-block;
}

.nav-link {
    display: block;
    padding: 0.5rem 1rem;
    color: #ffffff;
    text-decoration: none;
}

.dropdown-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    min-width: 10rem;
    padding: 0.5rem 0;
    margin: 0;
    font-size: 1rem;
    color: #212529;
    text-align: left;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: 0.25rem;
}

.nav-item:hover .dropdown-menu {
    display: block;
}

.dropdown-item {
    display: block;
    width: 100%;
    padding: 0.25rem 1.5rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
}

.dropdown-item:hover,
.dropdown-item:focus {
    color: #fff;
    text-decoration: none;
    background-color: #34C759;
}

.nav-login{
    max-width: 140px;
}

.form-control{
    background-color: #FFFFFF;
    color: #2f2c2c;
    border-radius: 5px;
}

.search-input {
    display: flex;
}

.search-input__form {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.search-input__label {
    display: flex;
    align-items: center;
    flex-grow: 1;
    margin-bottom: 0;
}

.active{
    border-bottom: 1px solid #34C759;
}

/*поле поиска*/

.search-input__label{
    position: relative;
}

.search-input__control{
    padding: 10px 30px 10px 10px;
    border-radius: 35px;
    color: #1A1A1A;
    border: none;
}

.btn-search {
    position: absolute;
    border: none;
    right: 15px;
    top: 10px;
    bottom: 5px;
    cursor: pointer;

    background-color: rgba(255, 255, 255, 0);
}

.btn-search__icon{

}

.nav-link{
    font-weight: 300 !important;
}

.nav-link__category{
    color: black;
}

/*главная страница*/

.top {
    padding: 80px 0 100px;
    background-color: #252525;
}

.top__inner {
     display: flex;
     justify-content: space-between;
     align-items: center;
 }

.title {
    max-width: 555px;
}
.title__text {
    margin: 0 auto;
    font-size: 64px;
    line-height: 75px;
    font-weight: 400;
    letter-spacing: 0.1em;
    font-family: "Roboto", sans-serif;
}


.title__text-name{
     color: #34C759;
     font-size: 64px;
     line-height: 75px;
     font-weight: 400;
     letter-spacing: 0.1em;
 }

.title__subtitle{
     font-weight: 400;
