 }

.new-product{
    text-align: center;
    font-size: 28px;
    margin: 60px auto;
    font-weight: 700;
    letter-spacing: 0.4em;
}

.popular{
    margin-bottom: 40px;
}

/*админ панель*/
.list-group{
    text-align: left;
    margin: 30px auto;
}
.list-group__title{
    text-align: center;
    font-size: 48px;
}

.list-group-item{
