}

/*обновить товар/изменить*/

.dashboard-create__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
}

.dashboard__category {
    width: 100%;
    margin-bottom: 20px;
    background-color: #ffff;
}

.dashboard__update {
    margin-bottom: 20px;
    width: 100%;
}

.dashboard__img-item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    padding: 10px;
    background-color: #f2f2f2;
    color: #1A1A1A;
    border-radius: 5px;
    cursor: pointer;
}

.dashboard__img-item input[type="file"] {
    display: none;
}

.dashboard-img__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
    background-color: #f2f2f2;
    border-radius: 5px;
    overflow: hidden;
}

.dashboard-img__photo {
    object-fit: cover;
    max-width: 100%;
    max-height: 100%;
}

.dashboard__update-control {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    font-family: inherit;
    line-height: 1.5;
    color: #333;
    background-color: #fff;
    transition: border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out;
}

.dashboard__update-control:focus {
    outline: none;
    box-shadow: 0 0 0 0.2rem #34C759;
}

.dashboard__update-btn {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 600;
    color: #1A1A1A;
    background-color: #FFFFFF;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.15s ease-in-out;
}

.dashboard__update-btn:hover {
    background-color: #34C759;
    color: #FFFFFF;
}

