    align-items: center;
    border: none;
    padding: 8px 25px 8px 10px;
    border-radius: 3px;
    cursor: pointer;
    position: relative;
    margin-right: 10px;
    transition: background-color 0.3s ease-in-out;
}


.menu-btn__details-icon{
    position: absolute;
    right: 5px;
    top: 10px;
    bottom: 10px;
}

.menu-btn__card {
    align-items: center;
    background-color: #4caf50;
    color: #fff;
    border: none;
    position: relative;
    padding: 8px 30px 8px 10px;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}


.menu-btn__card:hover {
    background-color: #388e3c;
}

.menu-card__btn {
    display: flex;
    justify-content: space-between;
}



