}

/*подробее о товаре*/

.detail {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    padding: 100px 10px;
    margin: 20px auto;
    background-color: #1A1A1A;
