}

.detail__img {
    border-radius: 5px;
    margin-right: 20px;
}

.detail__item {
    flex-basis: 50%;
    margin-top: 20px;
}

.detail__info {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
}

.detail__title {
    font-size: 24px;
    font-weight: bold;
