/* Burger Menu */
.navbar-toggle {
    display: none;
    cursor: pointer;
    padding: 30px;
}

.navbar-toggle__bar {
    width: 25px;
    height: 3px;
    background-color: #3e8e41;
    margin-bottom: 5px;
}

/* Active state */
.navbar-toggle.active .navbar-toggle__bar {
    background-color: #555;
}

.navbar-nav.active {
    display: flex;
    flex-direction: column;
}

@media (max-width: 927px) {
    .navbar-toggle {
        display: block;
    }

    .navbar-nav {
        display: none;
    }

    .navbar-nav.active {
        display: flex;
        flex-direction: column;
        background-color: #252525;
        padding: 10px;
    }
